import Vue from 'vue'
import App from './App.vue'
import vuetify from './plugins/vuetify'
import router from './router'

Vue.config.productionTip = false

import Message from '@/components/message/index.js'
Vue.prototype.$message = Message

new Vue({
  vuetify,
  router,
  render: h => h(App)
}).$mount('#app')
